import "./intro.scss";
import { init } from "ityped";
import {useEffect, useRef} from "react";
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';

export default function Intro() {
    const textRef = useRef();
    useEffect(() => {
        init(textRef.current,{
            
            showCursor: true,
            backDelay:  1500,
            backSpeed:  30,
            typeSpeed:  200,
            
            strings: ['CHEHAN SIVARUBAN' , 'CHIKAAAAA ðŸ˜‰' ] })
    }, []);

    return (
        <div className = "intro" id = "intro">
          <div className="left">
            <div className="imageContainer">
                <img src="assets/mypng.png" alt="me" />
            </div>
          </div>
          <div className="right">
            <div className="wrapper">
                <h2>Hi There, I'm</h2>
                <h1> <span ref={textRef}></span></h1>
                <br></br>
                <br></br>
                <br></br>
                <br></br>
                <br></br>
                <div class="animated-text">
      <div class="line">Software engineering undergraduate</div>
      
      <div class="line">Frontend developer</div>
     
      <div class="line">Backend Developer</div>
      
      <div class="line">Maybe a fullstack developer</div>
    </div>
                <div className="icons">
                    <a href="https://www.facebook.com/profile.php?id=100009063200001">
            <FacebookIcon style={{ fontSize: 40 }}/></a>
            <a href="https://www.instagram.com/chikaaaaa___/">
                 <InstagramIcon style={{ fontSize: 40 }}/></a>
                 <a href="https://www.linkedin.com/in/chehan-sivaruban">
                 <LinkedInIcon style={{ fontSize: 40 }}/></a>
                 <a href="https://www.github.com/Chehansivaruban">
                 <GitHubIcon style={{ fontSize: 40 }}/></a>
            </div>
            </div>
            <div className="down">
            <a href="#portfolio">
                <img src="assets/arrow.png" alt="" />
            </a></div>
          </div>
        </div>
    )
}
